import React, { Component } from 'react';
import * as ethers from 'ethers/index';
import Config from '../../../contract/config.json'
import { Event } from '../../../contract/index'
// import * as weblib from '../../lib/web3.ts';
import queryString from 'query-string';


import { observer, inject } from 'mobx-react';

@inject('ethereumStore')
@observer
export default class Reading extends Component {
    constructor(props) {
        super(props);
        let { query } = queryString.parseUrl(window.location.hash.substr(1));
        this.state = {
            title: this.props.ethMail.object.title,
            recipients: [this.props.ethMail.object.send],
            body: this.props.ethMail.object.body,
            mailId: this.props.ethMail.object.mailId || query.mailId,
        };
        const ethMail = Config.ethMail;
        this.contract = new ethers.Contract(ethMail[this.props.ethereumStore.networkId].contractAddress, ethMail.abi, this.props.ethereumStore.provider);

    }


    componentWillMount = async () => {
        let event = Event.bind(this.props.ethereumStore.provider);
        let [mailId, send, title, time, body] = [].concat(...await event(this.contract, 'MailInfo', [this.state.mailId]));
        console.log(mailId, send, title, time, body)
        this.setState({
            mailId, recipients: [send], title, time, body
        })

    }

    submit = async (e) => {
        e.preventDefault();
        let sender = this.contract.connect(this.props.ethereumStore.wallet.connect(this.props.ethereumStore.provider));
        let tx = await sender.send(this.state.recipients, [], this.state.title, this.state.body, this.state.mailId)

        alert('txid: ' + tx.hash)
        setTimeout(async () => {
            await tx.wait();
        })
    }

    render() {
        return (
            <form>
                <div className="box box-primary">
                    <div className="box-header with-border">
                        <h3 className="box-title">寫信</h3>
                    </div>
                    <div className="box-body">
                        <div className="form-group">
                            <label>標題</label>
                            <input className="form-control" placeholder="Title" maxLength="50" value={'Re:' + this.state.title} disabled />
                        </div>
                        <div className="form-group">
                            <label>收件人</label>
                            <input className="form-control" placeholder="Recipient" maxLength="42" value={this.state.recipients[0]} disabled />
                            {/* <div className="row">

                            </div> */}
                        </div>
                        {/* <div className="form-group">
                            <label>副本</label>
                            <input className="form-control" placeholder="CC" maxLength="42" onChange={this.activeCC} />
                            <div className="row">

                            </div>
                        </div> */}
                        <div className="form-group">
                            <label>內容</label>
                            <textarea
                                onChange={(e) => { this.setState({ body: e.target.value }) }}
                                className="form-control"
                                rows="3"
                                value={this.state.body}
                                placeholder="Text"
                                style={{ "margin": "0px 3px 0px 0px", "width": "100%", "height": "291px" }}>
                            </textarea>
                        </div>
                    </div>
                    <div className="box-footer">
                        <button className="pull-left btn btn-primary" onClick={this.submit}>回覆</button>
                    </div>
                </div>
            </form>
        )
    }
}
export { Reading };